@import "https://fonts.googleapis.com/css2?family=Kantumruy+Pro:ital,wght@0,100..700;1,100..700&display=swap";
@import "https://fonts.googleapis.com/css2?family=Long+Cang&display=swap";
@import "https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined:opsz,wght,FILL,GRAD@20..48,100..700,0..1,-50..200";
:root {
  --ratio: 1.067;
}

* {
  font-optical-sizing: auto;
  font-family: Kantumruy Pro, sans-serif;
  font-style: normal;
  font-weight: 400;
}

h1, h2, h3, h4, h5, p {
  margin: 0;
}

b {
  font-weight: 600;
}

h2 {
  margin-top: 3rem;
  margin-bottom: 1.5rem;
}

h3 {
  margin-bottom: .25rem;
  font-size: 1rem;
}

p {
  margin-bottom: 1rem;
  font-size: 1rem;
}

small {
  font-size: .75rem;
  line-height: 0;
}

:root {
  --dark: black;
  --light: white;
  --color: #ff421c;
  --neut: #f3f3f2;
}

body {
  margin: 0;
}

.material-symbols-outlined {
  font-variation-settings: "FILL" 1, "wght" 400, "GRAD" 0, "opsz" 24;
}

.template {
  display: none;
}

header {
  background-color: var(--dark);
  color: var(--light);
}

header .title {
  text-align: center;
  padding: 2rem 0 1.5rem;
  font-family: Long Cang, cursive;
  font-size: 3rem;
  font-style: normal;
  font-weight: 400;
}

main {
  max-width: 1000px;
  margin: auto;
  padding: 0 clamp(.5rem, 2vw, 2rem) 10vmin;
}

.nav-item-flex {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

aside small {
  margin-bottom: .75rem;
  display: inline-block;
}

nav {
  --gap: 1rem;
  gap: var(--gap);
  width: 100%;
  display: flex;
  overflow-x: scroll;
}

nav .category-name {
  text-transform: capitalize;
  margin: 0;
  display: none;
}

nav .category-name:before {
  content: "— ";
}

nav .item-wrapper {
  gap: var(--gap);
  display: flex;
}

nav .item {
  text-transform: capitalize;
  text-wrap: nowrap;
  background-color: var(--dark);
  color: var(--light);
  border: none;
  padding: .5rem;
}

.products {
  --gap: 1.5rem;
  --itemsPerRow: 2;
  gap: 3rem var(--gap);
  flex-wrap: wrap;
  margin: auto;
  list-style-type: none;
  display: flex;
}

li {
  width: calc((100% - var(--gap) * (var(--itemsPerRow)  - 1)) / var(--itemsPerRow));
}

.product .product-image-wrapper {
  aspect-ratio: 4 / 5;
  background-color: var(--neut);
  justify-content: center;
  align-items: center;
  margin-bottom: .5rem;
  display: flex;
  position: relative;
}

.product .discount {
  background-color: var(--color);
  color: var(--light);
  padding: .25rem .25rem .25rem 1rem;
  position: absolute;
  top: 1rem;
  right: 0;
}

.product img {
  max-width: 100%;
  max-height: 100%;
}

.product .brand-rating-wrapper {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.product .brand {
  text-transform: uppercase;
}

.product .rating.material-symbols-outlined {
  font-size: 1rem;
}

.product .description {
  -webkit-line-clamp: 2;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  display: -webkit-box;
  overflow: hidden;
}

.product .price {
  flex-direction: row-reverse;
  justify-content: space-between;
  gap: .5rem;
  display: flex;
}

.product .original-price {
  color: var(--faint);
  text-decoration: line-through;
}

.product .add-to-cart {
  border: 1px solid var(--dark);
  cursor: pointer;
  background-color: #0000;
  width: 100%;
  padding: .5rem;
}

footer {
  background-color: var(--dark);
  color: var(--light);
  flex-direction: column;
  gap: 1rem;
  height: 300px;
  padding: 2rem;
  display: flex;
}

footer a {
  color: inherit;
  margin-right: 1rem;
}

@media (width >= 600px) {
  .products {
    --itemsPerRow: 3;
  }
}

@media (width >= 800px) {
  .nav-item-flex {
    flex-direction: row;
  }

  nav {
    --gap: 1.5rem;
    flex-direction: column;
    width: 200px;
    overflow: hidden;
  }

  nav .category-name, nav .item-wrapper {
    display: block;
  }

  nav .item {
    color: var(--dark);
    cursor: pointer;
    background-color: #0000;
    padding-left: 0;
    display: block;
  }

  nav .item:hover {
    text-decoration: underline;
  }

  .products {
    --gap: 1.5rem;
    --itemsPerRow: 3;
  }
}

@media (width >= 1000px) {
  .products {
    --gap: 1.5rem;
    --itemsPerRow: 4;
  }
}
